import React from 'react';
import classNames from 'classnames';
import SectionFooter from '../SectionFooter';

import Button from '../Button';

import photo from '../../images/road/photoRoad.png';
import mobPhoto from '../../images/mobPhoto.png';
import s from './index.module.css';
import { useBreakpoints } from '../../utils/hooks/useBreakpoints';

export default function RoadForWhom({ openModal }) {
  const { isMobile } = useBreakpoints();
  const list = [
    '             .',
    '    ,       .',
    '       ,      .',
  ];

  return (
    <div>
      <div className={classNames('container', s.roadForWhomWrapper)}>
        <div className={s.leftSide}>
          {!isMobile && (
            <>
              <span className={s.subtitle}>Road</span>
              <h2 className={s.title}> </h2>
            </>
          )}
          <span className={s.description}>
                      
            ?   ,    
              ?
          </span>
          <span className={s.toGetTitle}>
                
          </span>
          <span className={s.name}>  </span>

          {!isMobile && (
            <Button
              className={s.btn}
              onClick={openModal}
              text={<span className={s.btnText}></span>}
            />
          )}
        </div>
        <div className={s.photoContainer}>
          {isMobile ? (
            // TODO add shadow to photo in mob
            <>
              <img
                alt="photo_background_mob"
                className={s.photo}
                src={mobPhoto}
              />
              <h2 className={classNames(s.title, s.titleMob)}> </h2>
            </>
          ) : (
            <img alt="photo_background" className={s.photo} src={photo} />
          )}
        </div>
        <div className={s.rightSide}>
          {!isMobile && (
            <span className={classNames(s.subtitle, s.happyness)}>
              happyness
            </span>
          )}
          <ul className={s.list}>
            {list.map((el, i) => {
              return <li key={i}>{el}</li>;
            })}
          </ul>
        </div>
      </div>
      <SectionFooter text="      " />
    </div>
  );
}
